<template>
  <div id="app">
    <div class="mx-auto w-100" style="max-width: 580px">
      <b-navbar toggleable="lg" type="dark" variant="primary">
        <b-navbar-brand href="#">12</b-navbar-brand>
      </b-navbar>

      <div>
        <b-card>
          <b-card-text>
            벙주
            <b-form-input
              id="owner_name"
              type="text"
              placeholder="벙주 이름 (ex. 박태현)"
              v-model="ownerName"
              required
            ></b-form-input>
          </b-card-text>

          <b-card-text>
            <b-form-group label="벙주 성별" v-slot="{ ariaDescribedby }">
            <b-form-radio-group
              v-model="ownerSex.selected"
              :options="ownerSex.options"
              :aria-describedby="ariaDescribedby"
              name="sex_type"
            ></b-form-radio-group>
            </b-form-group>
          </b-card-text>

          <b-card-text>
            <b-form-group label="벙 종류" v-slot="{ ariaDescribedby }">
            <b-form-radio-group
              v-model="classType.selected"
              :options="classType.options"
              :aria-describedby="ariaDescribedby"
              name="class_type"
            ></b-form-radio-group>
            </b-form-group>
          </b-card-text>

          <b-card-text>
            차수
            <b-form-input
              id="order_cnt"
              type="number"
              placeholder="0"
              :formatter="formatOrderCnt"
              v-model="orderCnt"
              required
            ></b-form-input>
          </b-card-text>

          <div class="mb-4" v-for="(i, i_index) in Number(orderCnt)" v-bind:key="i">
            <h6>{{i}}차</h6>
            <b-card-text>
            인원 수 (벙주포함, 최대 60명)
            <b-form-input
              id="people_cnt"
              type="number"
              placeholder="0"
              :formatter="formatPeopleCnt"
              v-model="orderInfo[i_index].peopleCnt"
              required
              ></b-form-input>
            </b-card-text>

            <b-card-text>
              <b-row>
                <b-col md="6" class="mb-2 w-50">
                  <label for="sb-disabled">남자</label>
                  <b-form-spinbutton min="0" :max="orderInfo[i_index].peopleCnt" v-model="orderInfo[i_index].male"></b-form-spinbutton>
                </b-col>
                <b-col md="6" class="mb-2 w-50">
                  <label for="sb-readonly" class="">여자</label>
                  <b-form-spinbutton min="0" :max="orderInfo[i_index].peopleCnt" v-model="orderInfo[i_index].female"></b-form-spinbutton>
                </b-col>
              </b-row>
            </b-card-text>

            <b-card-text>
              {{i}}차 금액
              <b-form-input
                id="total_price"
                type="number"
                placeholder="0"
                v-model="orderInfo[i_index].totalPrice"
                required
              ></b-form-input>
            </b-card-text>

            <b-badge href="#" v-if="orderInfo[i_index].male > 0" variant="primary">남자</b-badge>
            <b-card-text v-for="(j, j_index) in orderInfo[i_index].male" v-bind:key="'male_' + j">
              참가자{{ j }} {{ (j_index == 0 && ownerSex.selected == 'm')? "(벙주)":"" }}
              <b-form-input
                type="text"
                placeholder="참가자 이름"
                :disabled="(j_index == 0 && ownerSex.selected == 'm')? true:false"
                v-bind:value="(j_index == 0 && ownerSex.selected == 'm')? ownerName:''"
                required
              ></b-form-input>
            </b-card-text>

            <hr>

            <b-badge href="#" v-if="orderInfo[i_index].female > 0" variant="warning">여자</b-badge>
            <b-card-text v-for="(j, j_index) in orderInfo[i_index].female" v-bind:key="'female_' + j">
              참가자{{ j }} {{ (j_index == 0 && ownerSex.selected == 'f')? "(벙주)":"" }}
              <b-form-input
                type="text"
                placeholder="참가자 이름"
                :disabled="(j_index == 0 && ownerSex.selected == 'f')? true:false"
                v-bind:value="(j_index == 0 && ownerSex.selected == 'f')? ownerName:''"
                required
              ></b-form-input>
            </b-card-text>
          </div>
        </b-card>
      </div>
      
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ownerName: null,  // 벙주 이름
      ownerSex: {       // 벙주 성별 - m: 남자, f: 여자
        selected: 'm',
        options: [
          { text: '남자', value: 'm'},
          { text: '여자', value: 'f' },
        ]
      },
      maxPeopleCnt: 60, // 최대 인원수
      classType: {      // 벙 종류
        selected: 'drink',
        options: [
          { text: '술', value: 'drink', disable: true},
          { text: '운동', value: 'exercise' },
          { text: '카페', value: 'cafe' },
          { text: '집', value: 'home' },
          { text: '기타', value: 'etc' },
        ]
      },
      orderCnt: 1,    // 차수
      orderInfo: [    // 회차별 정보
        {
          peopleCnt: 1, 
          totalPrice: 10000, 
          male: 1, 
          female: 0,
          malePeople: [],
          femalePeople: [],
        },
      ],
      orderInfoClone: null,
    }
  }, 
  computed: {
    orderInfoCloneItems: function() {
      return JSON.parse(JSON.stringify(this.orderInfo))
    }
  },
  mounted() {
    console.log(this.orderInfo.length);
  },
  methods: {
    formatOrderCnt(e){
      return String(e).substring(0,2);
    },
    formatPeopleCnt(e){
      return String(e).substring(0,2);
    }
  }, 
  watch: {
    orderCnt: {
      handler(newData, oldData) {
        if (newData > oldData) {
          // 차수가 늘어남에 따라 새로운 인원수, 금액을 배열에 담는다
          for (let i=0; i < newData - oldData; i++) {
            this.orderInfo.push({
              peopleCnt: 1, 
              totalPrice: 10000, 
              male: 1, 
              female: 0,
              malePeople: [],
              femalePeople: [],
            });
          }
        }
        else {
          // 차수가 줄어듦에 따라 순차적으로 제거한다
          for (let i=0; i < oldData - newData; i++) {
            this.orderInfo.pop();
          }
        }
      }
    }, 
    // 인원수 변화 감지
    orderInfoCloneItems: {
      handler: function(newData, oldData) {
        for (var i=0; i < newData.length; i++) {
          // 총 인원수 변경
          if (newData[i].peopleCnt != oldData[i].peopleCnt) {
            this.orderInfo[i].male = Number(this.orderInfo[i].male) + Number(newData[i].peopleCnt) - Number(oldData[i].peopleCnt);
          }
          // 남성 비율 변경
          if (newData[i].male != oldData[i].male) {
            console.log(Number(newData[i].peopleCnt) - Number(oldData[i].male));
            this.orderInfo[i].female = Number(newData[i].peopleCnt) - Number(newData[i].male);
          }
          // 여성 비율 변경
          if (newData[i].female != oldData[i].female) {
            console.log(Number(newData[i].peopleCnt) - Number(oldData[i].female));
            this.orderInfo[i].male = Number(newData[i].peopleCnt) - Number(newData[i].female);
          }
        }
      },
    },
  },
}
</script>


<style>
nav a {
  font-weight: bold;
}
h6 {
  display: flex;
  flex-direction: row;
}
h6:before, h6:after{
  content: "";
  flex: 1 1;
  border-bottom: 1px solid;
  margin: auto;
}
h6:before {
  margin-right: 10px
}
h6:after {
  margin-left: 10px
}
